
import {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonMenu,
    IonList,
    IonItem,
    IonMenuButton,
    menuController,
    onIonViewDidLeave,
    IonButton,
    IonIcon,
    loadingController,
    IonLabel,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    IonNote,
    IonFooter
} from '@ionic/vue';
import { computed, defineComponent, ref } from 'vue';
import { searchSharp } from 'ionicons/icons';
import NeopleApi from '@/utils/NeopleApi';
import Common from '@/utils/Common';

export default defineComponent({
    components: {
        IonContent,
        IonHeader,
        IonPage,
        IonTitle,
        IonToolbar,
        IonSearchbar,
        IonButtons,
        IonMenu,
        IonList,
        IonItem,
        IonMenuButton,
        IonButton,
        IonIcon,
        IonLabel,
        IonGrid,
        IonRow,
        IonCol,
        IonText,
        IonNote,
        IonFooter
    },
    setup() {
        const player = ref('');
        let playerId = '';
        let playerInfo = ref();

        const playerSearch = async () => {
            const loading = await loadingController.create({
                message: '데이터 조회중',
                mode: 'ios'
            });
            loading.present();

            const playerIdJson = await NeopleApi.cyPlayerId({ nickname: encodeURI(player.value) });

            if (Common.isNull(playerIdJson.rows)) {
                playerId = '';
                playerInfo.value = ref();
            } else {
                playerId = playerIdJson.rows[0].playerId;
                playerInfo.value = ref(await NeopleApi.cyPlayerInfo({ playerId: playerId })).value;
                console.log(playerInfo.value);
            }

            loading.dismiss();
        };

        const enter = (e: KeyboardEvent) => {
            if (e.key === 'Enter') {
                setTimeout(() => {
                    playerSearch();
                }, 250);
            }
        };

        const gameNormalRatio = computed(() => {
            let ratio = 0;

            if (!Common.isNull(playerInfo) && !Common.isNull(playerInfo.value.records)) {
                const game = (playerInfo.value.records as any[]).filter(game => game.gameTypeId === 'normal')[0];
                ratio = Math.round(game.winCount / (game.winCount + game.loseCount) * 100);
            }

            return ratio;
        });

        const gameNormalRatioColor = computed(() => {
            let color = '';

            if (gameNormalRatio.value >= 75) {
                color = 'primary';
            } else if (gameNormalRatio.value >= 50) {
                color = 'success';
            } else if (gameNormalRatio.value >= 25) {
                color = 'warning';
            } else {
                color = 'danger';
            }
            return color;
        });

        onIonViewDidLeave(() => {
            menuController.close('main-menu');
        });

        return {
            searchSharp,
            player,
            playerSearch,
            playerInfo,
            enter,
            gameNormalRatio,
            gameNormalRatioColor
        }
    }
});
